.pokemon-card {
  position: relative;
  border-top: 1px solid gray;
  padding: 10px 0; }
  .pokemon-card__container {
    -webkit-transition: .5s filter ease-out;
    -o-transition: .5s filter ease-out;
    transition: .5s filter ease-out; }
  .pokemon-card__container--loading {
    -webkit-filter: blur(5px);
    filter: blur(5px); }
  .pokemon-card__avatar {
    background-position: center;
    background-size: cover; }
    .pokemon-card__avatar:before {
      content: '';
      padding-top: 100%;
      display: inline-block; }
  .pokemon-card__details {
    text-transform: capitalize; }



