.error-handler {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 1em;
  text-align: center;
  background-color: indianred;
  color: white; }
  .error-handler.error-handler-enter {
    top: -100%; }
    .error-handler.error-handler-enter.error-handler-enter-active {
      top: 0;
      -webkit-transition: top 2s ease-in;
      -o-transition: top 2s ease-in;
      transition: top 2s ease-in; }
  .error-handler.error-handler-leave.error-handler-leave-active {
    top: -100%;
    -webkit-transition: top 2s ease-out;
    -o-transition: top 2s ease-out;
    transition: top 2s ease-out; }



