.pagination-container {
  margin-bottom: 1em; }
  .pagination-container__divider {
    height: 1em; }
  .pagination-container .pagination-container input {
    margin-bottom: 0;
    width: 60px; }
  .pagination-container .pagination {
    margin-bottom: 0; }
  .pagination-container .pagination li {
    display: inline-block; }
  .pagination-container .button-group {
    margin-bottom: 0;
    margin-left: 10px; }
  .pagination-container button {
    cursor: pointer; }
  .pagination-container .pagination-next button:after {
    display: inline-block;
    margin-left: 0.5rem;
    content: '\BB'; }
  .pagination-container .pagination-previous button:before {
    display: inline-block;
    margin-right: 0.5rem;
    content: '\AB'; }



